import { FunctionComponent } from 'preact';
import { useMemo } from 'preact/hooks';
import { ChevronRight } from '@adobe-commerce/elsie/icons';
import { classes, Slot } from '@adobe-commerce/elsie/lib';
import { IconConfig, ReturnsListContentProps } from '@/order/types';
import {
  Card,
  ContentGrid,
  Header,
  Icon,
  Image,
  Pagination,
} from '@adobe-commerce/elsie/components';
import EmptyList from '../EmptyList';
import '@/order/components/ReturnsListContent/ReturnsListContent.css';
import { formatReturnStatus } from '@/order/lib/returnOrdersHelper';
import { Text } from '@adobe-commerce/elsie/i18n';
import { CardLoader } from '@/order/components';
import { capitalizeFirst } from '@/order/lib/capitalizeFirst';

const iconConfig: IconConfig = { size: '32', stroke: '2' };

export const ReturnsListContent: FunctionComponent<ReturnsListContentProps> = ({
  placeholderImage,
  minifiedViewKey,
  withReturnNumber = false,
  withOrderNumber = false,
  slots,
  pageInfo,
  withReturnsListButton = true,
  isMobile = false,
  returnsInMinifiedView = 1,
  translations = {},
  orderReturns = [],
  minifiedView = false,
  withHeader = true,
  withThumbnails = true,
  selectedPage = 1,
  handleSetSelectPage,
  routeReturnDetails,
  routeOrderDetails,
  routeTracking,
  routeReturnsList,
  routeProductDetails,
  loading,
}) => {
  const lastElementNumber = minifiedView
    ? returnsInMinifiedView
    : orderReturns.length;

  const ImageTag = routeProductDetails?.() ? 'a' : 'span';

  const renderReturnOrders = useMemo(() => {
    return orderReturns.slice(0, lastElementNumber).map((element, i) => {
      const items = element?.items ?? [];

      const headerTotalProductValue = items.reduce(
        (accumulator: number, value): number => {
          return (value.requestQuantity ?? 0) + accumulator;
        },
        0
      );

      return (
        <Card
          key={i}
          variant="secondary"
          className="order-returns-list-content__cards-list"
        >
          <div className="order-returns-list-content__cards-grid">
            <div className="order-returns-list-content__descriptions">
              <p className="order-returns-list-content__return-status">
                {formatReturnStatus(element.returnStatus) ? (
                  <Text
                    id={`Order.Returns.${minifiedViewKey}.returnsList.returnStatus.${formatReturnStatus(
                      element.returnStatus
                    )}`}
                  />
                ) : (
                  <span>{capitalizeFirst(element.returnStatus)}</span>
                )}
              </p>
              {withReturnNumber ? (
                <p>
                  {translations.returnNumber}{' '}
                  <a
                    href={
                      routeReturnDetails?.({
                        returnNumber: element.returnNumber,
                        orderNumber: element.orderNumber,
                        token: element.token,
                      }) ?? '#'
                    }
                    rel="noreferrer"
                  >
                    {element.returnNumber}
                  </a>
                </p>
              ) : null}
              {withOrderNumber ? (
                <p>
                  {translations.orderNumber}{' '}
                  <a
                    href={
                      routeOrderDetails?.({
                        orderNumber: element.orderNumber,
                        token: element.token,
                      }) ?? '#'
                    }
                    rel="noreferrer"
                  >
                    {element.orderNumber}
                  </a>
                </p>
              ) : null}
              {element?.tracking?.map((track, index) => {
                // No title in return tracking schema for return
                const trackingData = {
                  title: '',
                  number: track?.trackingNumber ?? '',
                  carrier: track?.carrier?.label ?? '',
                };

                const trackingRoute = routeTracking?.(trackingData);
                const key = `${trackingData.number}_${index}`;

                return (
                  <p key={key}>
                    {`${translations.carrier} `}
                    {`${trackingData.carrier?.toLocaleUpperCase()} | `}
                    {trackingRoute ? (
                      <a
                        href={trackingRoute}
                        target="_blank"
                        rel="noreferrer"
                        data-testid={`${key}_link`}
                      >
                        {track.trackingNumber}
                      </a>
                    ) : (
                      <span data-testid={`${key}_span`}>
                        {track.trackingNumber}
                      </span>
                    )}
                  </p>
                );
              })}
              {slots?.ReturnItemsDetails ? (
                <Slot
                  data-testid="returnItemsDetails"
                  name="ReturnItemsDetails"
                  slot={slots?.ReturnItemsDetails}
                  context={{ items: element.items }}
                />
              ) : null}

              {!slots?.ReturnItemsDetails && element.items.length ? (
                <p>
                  {headerTotalProductValue}{' '}
                  <Text
                    id={`Order.Returns.${minifiedViewKey}.returnsList.itemText`}
                    plural={headerTotalProductValue}
                    fields={{ count: headerTotalProductValue }}
                  />
                </p>
              ) : null}
            </div>

            {withThumbnails ? (
              <ContentGrid
                maxColumns={isMobile ? 3 : 9}
                emptyGridContent={<></>}
                className={classes([
                  'order-returns-list-content__images',
                  ['order-returns-list-content__images-3', isMobile],
                ])}
              >
                {element?.items?.map((item, index) => {
                  const key = `key_${index}_${item.uid}`;
                  const label = item?.product?.name ?? '';
                  const src = item?.thumbnail?.url ?? placeholderImage ?? '';

                  const defaultImageProps = {
                    title: label,
                    src,
                    alt: label,
                    width: 85,
                    height: 114,
                    loading: 'eager',
                  } as const;

                  return (
                    <ImageTag
                      data-testid={key}
                      key={key}
                      href={routeProductDetails?.(item) ?? '#'}
                    >
                      <Slot
                        slotTag="span"
                        contentTag="span"
                        name="ReturnListImage"
                        slot={slots?.ReturnListImage}
                        context={{
                          data: item,
                          defaultImageProps,
                        }}
                      >
                        <Image {...defaultImageProps} />
                      </Slot>
                    </ImageTag>
                  );
                })}
              </ContentGrid>
            ) : null}
            {slots?.DetailsActionParams ? (
              <Slot
                className="order-returns-list-content__actions"
                data-testid="detailsActionParams"
                name="DetailsActionParams"
                slot={slots?.DetailsActionParams}
                context={{ returnOrderItem: element }}
              />
            ) : (
              <a
                href={
                  routeReturnDetails?.({
                    returnNumber: element.returnNumber,
                    token: element.token,
                    orderNumber: element.orderNumber,
                  }) ?? '#'
                }
                className="order-returns-list-content__actions"
              >
                <Icon source={ChevronRight} {...iconConfig} />
              </a>
            )}
          </div>
        </Card>
      );
    });
  }, [
    orderReturns,
    lastElementNumber,
    minifiedViewKey,
    withReturnNumber,
    translations,
    withOrderNumber,
    slots,
    withThumbnails,
    isMobile,
    placeholderImage,
    ImageTag,
    routeTracking,
    routeProductDetails,
    routeReturnDetails,
    routeOrderDetails,
  ]);

  const renderMinifiedView = useMemo(
    () => (
      <>
        {withHeader ? (
          <Header
            title={translations.minifiedViewTitle}
            divider={false}
            className={'order-returns__header--minified'}
          />
        ) : null}
        {loading ? (
          <CardLoader withCard={false} />
        ) : (
          <>
            {renderReturnOrders}
            <EmptyList
              minifiedView={minifiedView}
              typeList="orders"
              isEmpty={!orderReturns.length}
              message={translations.emptyOrdersListMessage}
            />
            {withReturnsListButton ? (
              <a
                className="order-returns-list-content__actions"
                href={routeReturnsList?.() ?? '#'}
              >
                <Card
                  variant="secondary"
                  className="order-returns-list-content__card"
                >
                  <div className={'order-returns-list-content__card-wrapper'}>
                    <p>{translations.viewAllOrdersButton}</p>
                    <Icon source={ChevronRight} {...iconConfig} />
                  </div>
                </Card>
              </a>
            ) : null}
          </>
        )}
      </>
    ),
    [
      routeReturnsList,
      withReturnsListButton,
      withHeader,
      translations,
      renderReturnOrders,
      minifiedView,
      orderReturns.length,
      loading,
    ]
  );

  const renderFullSizeView = useMemo(
    () => (
      <>
        {withHeader ? (
          <Header
            title={translations.minifiedViewTitle}
            divider={true}
            className={'order-returns__header--full-size'}
          />
        ) : null}
        {loading ? (
          <CardLoader withCard={false} />
        ) : (
          <>
            <EmptyList
              minifiedView={minifiedView}
              typeList="orders"
              isEmpty={!orderReturns.length}
              message={translations.emptyOrdersListMessage}
            />
            {renderReturnOrders}
            {pageInfo?.totalPages > 1 ? (
              <Pagination
                totalPages={pageInfo?.totalPages}
                currentPage={selectedPage}
                onChange={handleSetSelectPage}
              />
            ) : null}
          </>
        )}
      </>
    ),
    [
      renderReturnOrders,
      minifiedView,
      orderReturns,
      translations,
      pageInfo?.totalPages,
      selectedPage,
      handleSetSelectPage,
      loading,
      withHeader,
    ]
  );

  return (
    <div className="order-returns-list-content">
      {minifiedView ? renderMinifiedView : renderFullSizeView}
    </div>
  );
};
