import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes, useMemo } from 'preact/compat';
import { classes } from '@adobe-commerce/elsie/lib';
import { Icon, IllustratedMessage } from '@adobe-commerce/elsie/components';
import { EmptyBox } from '@adobe-commerce/elsie/icons';
import { EmptyListProps } from '@/order/types';
import '@/order/components/EmptyList/EmptyList.css';

export const EmptyList: FunctionComponent<EmptyListProps> = ({
  typeList,
  isEmpty,
  minifiedView,
  message,
}) => {
  const config = useMemo(() => {
    switch (typeList) {
      case 'orders':
        return {
          icon: EmptyBox,
          text: <p>{message}</p>,
          className: 'order-empty-list--empty-box',
        };
      default:
        return { icon: '', text: '', className:"" };
    }
  }, [typeList, message]);

  if (!isEmpty || !typeList || !config.text) return null;

  return (
    <IllustratedMessage
      className={classes([
        'order-empty-list',
      config.className,
        minifiedView ? 'order-empty-list--minified' : '',
      ])}
      message={config.text as VNode<HTMLAttributes<HTMLElement>>}
      icon={<Icon source={config.icon} />}
      data-testid="emptyList"
    />
  );
};
